.top
    height: 830px
    background-color: $color-bg
    padding: 0 10%
    position: relative
    &__wrapper
        transform: translateY(60%)
    &__title
        margin-bottom: 18px
        position: relative
        &::before
            position: absolute
            content: "Proven strategies backed by science for success."
            color: $color-text
            font-size: 16px
            font-weight: 400
            letter-spacing: -0.32px
            opacity: 0.4
            left: 0
            top: -55px
    &__text
        max-width: 545px
        font-size: 18px
        line-height: 160%
        margin-bottom: 18px
    &__img-box
        position: absolute
        top: -10%
        right: 5%
        background-position: center
        background-size: cover
        background-repeat: no-repeat
    &__img-level--box
        position: absolute
        width: 240px
        height: 105px
        background-color: #fff
        padding: 24px
        display: flex
        align-items: center
        justify-content: space-between
        box-shadow: 0px 16px 96px 0px rgba(111, 124, 144, 0.16)
        top:-52px
        left: -120px 
    &__decor
        width: 101px
        height: 17px
        position: absolute
        top: 15%
        left: 9%
    &__target
        width: 56px
        height: 56px
        margin-right: 16px
    &__img-title
        font-size: 24px
        color: $color-title
        font-weight: 800
        letter-spacing: -0.72px
    &__img-text
        font-size: 12px
        line-height: 24px
        letter-spacing: -0.12px

@media (max-width: 1350px)
    .top
        padding: 0 5%

@media (max-width: 1200px)
    h1
        font-size: 60px
    .top
        padding: 0 2%
        &__img-box
            right: 20px
        &__decor
            left: 1%
        &__text
            font-size: 16px
            max-width: 410px
        &__img-level--box
            padding: 10px
            width: 210px
            height: 75px
            left: -80px
        &__img
            width: 350px

@media (max-width: 900px)
    h1
        font-size: 50px
        max-width: 420px
    .top
        height: 640px
        &__img
            width: 300px
        &__target
            width: 45px
            margin-right: 0
        &__text
            max-width: 330px
        &__img-level--box
            width: 185px
            height: 70px
            left: -80px
        &__title::before
            font-size: 14px

@media (max-width: 768px)
    .top
        &__img-box
            right: 0
        &__img-level--box
            display: none
        &__wrapper
            transform: translateY(50%)

@media (max-width: 700px)
    h1
        max-width: 100%
        font-size: 40px
    .top
        min-height: 800px
        &__wrapper
            transform: translateY(20%)
        &__img-box
            right: 0
            transform: translateX(-60%)
        &__text-box
            padding-top: 400px
        &__text
            max-width: 100%
        &__decor
            display: none

@media (max-width: 540px)
    .top
        &__img-box
            transform: translateX(-47%)

@media (max-width: 540px)
    .top
        min-height: 760px
        &__wrapper
            transform: translateY(15%)
        &__img-box
            transform: translateX(-35%)
        &__text
            font-size: 15px
        &__img
            width: 250px
        &__text-box
            padding-top: 330px

@media (max-width: 400px)
    .top
        &__img-box
            transform: translateX(-22%)
        