@import "_variables"
@import "_settings"
@import "../../node_modules/swiper/swiper-bundle"
body
    font-size: 16px
    font-weight: 400
    color: $color-text
    letter-spacing: -0.32px
    line-height: 24px

h1
    font-size: 72px
    font-weight: 800
    line-height: 120%
    color: $color-title
    max-width: 500px

h2
    font-size: 48px
    font-weight: 800
    color: $color-title
    line-height: 56px
    letter-spacing: -0.48px
h3
    font-size: 24px
    font-weight: 700
    line-height: 32px
    letter-spacing: -0.72px
    color: $color-title-h2
.logo
    width: 149px
    height: 27.31px

.btn
    font-size: 14px
    padding: 11px 16px
    background-color: $color-green
    outline: none
    border: none
    cursor: pointer
    border-radius: 4px
    transition: background-color 0.3s ease-in-out
    font-size: 14px
    font-weight: 400
    color: #fff
    font-family: 'Kumbh Sans', sans-serif
    &:hover
        background-color: #157c6b



@import "components/_header"
@import "components/_top"
@import "components/_about"
@import "components/_comment"
@import "components/_blog"
@import "components/_free-guide"
@import "components/_place"
@import "components/_free-session"
@import "components/_subscribe"
@import "components/_footer"

@media screen and ( max-width:  1024px)
    h2
        font-size: 40px

@media screen and ( max-width:  800px)
    h2
        font-size: 30px
        line-height: 1.3
    h3
        font-size: 20px
    