.free-session
    margin-bottom: 100px
    &__wrapper
        width: 100%
        background-color: $color-bg
        border-radius: 24px
        min-height: 480px
        padding-left: 64px
        position: relative
    &__wrapper-left
        transform: translateY(70%)
    &__btn
        width: 250px
    &__h2
        font-size: 36px
        max-width: 550px
        line-height: normal
        margin-bottom: 20px
    &__text
        font-family: Inter
        font-size: 20px
        font-style: normal
        font-weight: 400
        line-height: 30px
        color: #4A556C
        opacity: 50%
        margin-bottom: 40px
    &__img
        position: absolute
        bottom: 0
        right: 0

@media (max-width: 1200px)
    .free-session__wrapper
        padding-left: 0

@media (max-width: 1130px)
    .free-session
        &__h2
            max-width: 450px
        &__img
            width: 50%
        &__wrapper
            min-height: 420px
        &__wrapper-left
            transform: translateY(30%)

@media (max-width: 800px)
    .free-session
        &__img
            position: static
            width: 70%
            border-radius: 15px
        &__wrapper-left
            transform: translateY(0)
        &__wrapper
            padding: 30px 0
            text-align: center
        &__h2
            max-width: 100%
            margin-bottom: 15px
            font-size: 30px
        &__text
            margin-bottom: 15px
            font-size: 18px
        &__btn
            margin-bottom: 15px