.swiper 
    width: 100%
    min-height: 400px

.comment
    background-color: $color-bg
    padding-top: 120px
    padding-bottom: 200px
    margin-bottom: 100px
    &__h2
        max-width: 530px
        margin-bottom: 40px
    &__item
        background: #FFF
        max-width: 400px
        height: 100%
        padding: 48px
        display: flex
        flex-direction: column
        row-gap: 15px
    &__text
        font-size: 24px
        font-weight: 400
        line-height: 32px
        letter-spacing: -0.72px
    &__avatar
        width: 70px
        height: 70px
    &__stars
        display: flex
        justify-content: space-between
        width: 116px
        height: 20px
        column-gap: 4px
    &__star
        fill: #FEC84B

@media (max-width: 1024px)
    .comment
        padding-top: 80px
        &__text
            font-size: 20px

@media (max-width: 900px)
    .comment
        margin-bottom: 60px
        padding-bottom: 60px