.place
    position: relative
    width: 446px
    height: 581px
    background: linear-gradient(210deg, #333461 -85.62%, #483D8B 25.45%, #353565 165.27%)
    &::after   
        content: ""
        background-image: url('../img/free-guide.png')
        position: absolute
        z-index: 2
        width: 341px
        height: 364px
        background-repeat: no-repeat
        background-size: cover
        background-position: center
        bottom: -0
        right: 0
    &__before
        position: relative
        left: 390px
        top: 0
        color: #fff
        font-weight: 800
        z-index: 10
        &::before
            display: block
            position: absolute
            content: ""
            z-index: -1
            left: -9px
            top: -34px  
            width: 96px
            height: 96px
            border-radius: 50%
            background-color: $color-green
    &__text
        position: absolute
        left: 40px
        top: 50px
        z-index: 15
        font-size: 48px
        font-weight: 800
        line-height: 130%
        color: #fff
        max-width: 240px
    &__img
        width: 45px
        height: 45px
    &__inner
        position: absolute
        bottom: 25px
        z-index: 5
        width: 100%
        height: 85px
        background: rgba(65, 58, 125, 0.30)
        color: #fff
        display: flex
        justify-content: center
        align-items: center
        column-gap: 5px
        cursor: pointer
        transition: background 0.3s ease-in-out
        &:hover
            background: rgba(65, 58, 125, 0.45)
            
    &__title
        font-weight: 700
        margin-bottom: -5px
    &__text-inner
        font-size: 10px
        font-weight: 500

@media (max-width: 1350px)
    .free-guide__right-wrapper
        padding-right: 30px

@media (max-width: 1024px)
    .place
        width: 400px
        height: 550px
        &__before
            left: 350px

@media (max-width: 800px)
    .place
        width: 350px
        height: 500px
        &__before
            left: 300px
        &__text
            font-size: 36px

@media (max-width: 700px)
    .place
        width: 350px
        height: 500px