.header
    height: 85px
    &__wrapper
        max-width: 1360px
        padding: 0 15px
        margin: 0 auto
        display: flex
        justify-content: space-between
        align-items: center
        height: 85px
    &__list
        display: flex
        justify-content: space-between
        align-items: center
        column-gap: 30px
    &__nav
        display: flex
        justify-content: space-between
        align-items: center
        column-gap: 30px
    &__link
        transition: 0.3s
        font-size: 15px
        &:hover
            color: $color-green
    &__btn
        font-weight: 600
.burger-menu
    display: none

@media (max-width: 800px)
    .header__nav
        display: none
    .menu123
        position: relative
        z-index: 6
        background: none
        border: 1px solid black
        display: flex
        flex-direction: column
        justify-content: space-between
        width: 55px
        row-gap: 15px
    .menu222
        width: 55px
        height: 3px
        color: black