.blog
    text-align: center
    margin-bottom: 255px
    &__h2
        max-width: 570px
        text-align: center
        margin: 0 auto
        margin-bottom: 65px
    &__box
        display: flex
        justify-content: space-between
        column-gap: 40px
        margin-bottom: 55px
    &__img
        width: 100%
        background-position: center
        background-size: cover
        background-repeat: no-repeat
    &__wrapper
        padding: 24px 28px
        text-align: left
        border: 1px solid #EDEFF5
        margin-top: -6px
    &__h3
        color: $color-title
        margin-bottom: 12px
        min-height: 64px
    &__btn
        width: 250px

@media (max-width: 800px)
    .blog
        margin-bottom: 100px
        &__box
            column-gap: 20px

@media (max-width: 800px)
    .blog
        &__box
            flex-wrap: wrap