.about
    position: relative
    padding-top: 160px
    margin-bottom: 80px
    &__decor
        width: 101px
        height: 17px
        margin-bottom: 32px
    &__h2
        max-width: 530px
        margin-bottom: 90px
    &__text-box
        position: absolute
        right: 0
        max-width: 500px
        top: 60px
        font-size: 17px
        font-weight: 400
        line-height: 25px
    &__text
        margin-bottom: 20px
    &__advant-box
        display: flex
        justify-content: space-evenly
        align-items: flex-start
    &__advant-item
        padding: 40px
    &__img
        width: 72px
        height: 72px
        margin-bottom: 32px
    &__h3
        margin-bottom: 20px

@media (max-width: 1024px)
    .about
        padding-top: 60px

@media (max-width: 950px)
    .about
        &__text-box
            position: static
            max-width: 100%
        &__h2
            margin-bottom: 30px
        &__advant-item
            padding: 15px

@media (max-width: 640px)
    .about
        margin-bottom: 50px
        &__advant-box
            flex-wrap: wrap
