.footer
    position: relative
    
    &__logo a svg
        margin-bottom: 32px
    &__wrapper
        padding: 64px 0
        display: flex
        justify-content: space-between
        border-bottom: 1px solid #E4E7EC
        margin-bottom: 32px
    &__logo-descr
        opacity: 0.5
        max-width: 320px
    &__title
        color: $color-title
        font-size: 14px
        font-style: normal
        font-weight: 800
        line-height: 20px
        margin-bottom: 16px
    &__pages-list
        display: flex
        flex-direction: column
        row-gap: 12px
    &__link
        color: rgba(74, 85, 108, 0.50)
        transition: color 0.3s ease-in-out
        &:hover
            color: $color-green
    &__contacts-link
        display: flex
        flex-direction: column
        row-gap: 16px
    &__social
        position: absolute
        right: 0
        display: flex
        align-items: center
        gap: 24px
        padding-bottom: 48px
    &__social-icon
        width: 24px
        height: 24px
        fill: #98A2B3
        transition: fill 0.3s ease-in-out
        &:hover
            fill: $color-green

@media (max-width: 800px)
    .footer__logo
        margin-right: 30px
        margin-bottom: 40px

@media (max-width: 668px)
    .footer
        text-align: center
        &__wrapper
            flex-wrap: wrap
            justify-content: center
        &__pages
            width: 100%
            margin-bottom: 30px
        &__social
            position: static
            display: inline-block
        &__social-link
            margin-right: 24px
            &:last-child
                margin-right: 0