.free-guide
    margin-bottom: 120px
    &__h2
        margin-bottom: 25px
    &__wrapper 
        display: flex
        justify-content: space-between
        align-items: center
    &__left-wrapper
        max-width: 500px
        margin-right: 20px
    &__text
        font-size: 22px
        line-height: 33px
        margin-bottom: 25px
    &__text-desr
        font-size: 17px
        font-weight: 400
        line-height: normal
        margin-bottom: 22px

@media (max-width: 1024px)
    .free-guide
        &__text
            font-size: 20px


@media (max-width: 768px)
    .free-guide
        text-align: center
        margin-bottom: 80px
        &__text
            font-size: 18px
        &__wrapper
            flex-wrap: wrap-reverse
            justify-content: center
        &__right-wrapper
            margin-bottom: 30px
            
