.subscribe
    background-color: $color-bg
    padding: 48px 0
    &__wrapper  
        display: flex
        justify-content: space-between
        align-items: center
    &__left-wrapper
        margin-right: 20px
    &__title
        font-size: 30px
        font-weight: 800
        line-height: 38px
        color: $color-title
        margin-bottom: 16px
    &__descr
        font-size: 20px
        font-weight: 400
        line-height: 30px
    &__input
        padding: 10px 14px
        border-radius: 8px
        border: 1px solid #D0D5DD
        box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05)
        margin-right: 16px
        width: 280px
        outline: none
        &::-webkit-input-placeholder
            color: rgba(74, 85, 108, 0.50)
            font-family: 'Kumbh Sans', sans-serif
    &__btn
        width: 150px
        font-weight: 500


@media screen and ( max-width: 1100px )
    .subscribe
        text-align: center
        &__wrapper
            flex-wrap: wrap
            justify-content: center
            row-gap: 20px
        &__input
            margin-bottom: 15px
            max-width: 600px